import React, { FC, useEffect } from 'react'

import AOS from 'aos'
import 'aos/dist/aos.css'

interface Props {
  bgSrc: string
}

const Example: FC<Props> = ({ bgSrc }) => {
  useEffect(() => {
    AOS.init()
  })

  return (
    <>
      <main
        data-aos="fade-right"
        data-aos-duration="2000"
        className="relative isolate min-h-full"
      >
        <img
          src={bgSrc}
          alt="sectionbg3"
          className="absolute inset-0 -z-10 h-full w-full object-cover object-top"
        />
        <div className="mx-auto max-w-7xl p-4 text-center sm:py-24 lg:px-8">
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-white sm:text-5xl">
            Hana Pension
          </h1>
          <p className="mt-4 text-sm text-white/70 sm:mt-6 sm:text-lg">
            지금 하나펜션에서 예약하고,
            <br />
            자연과 함께하는 휴식을 경험해보세요.
          </p>
          <div className="mt-10 flex justify-center">
            <a
              href="https://trip.ddnayo.com/main"
              className="text-sm font-semibold leading-7 text-white  md:text-xl"
            >
              지금 예약하기↗
            </a>
          </div>
        </div>
      </main>
    </>
  )
}

export default Example
