import React, { useEffect } from 'react'

import AOS from 'aos'
import 'aos/dist/aos.css'

import styled from '@emotion/styled'

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay } from 'swiper'

// Import Swiper styles
import 'swiper/css'
import 'swiper/css/pagination'

const RoomStyle = styled.div`
  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background: transparent;
    border: 1px solid #000;
    opacity: 1;
  }
  .swiper-pagination-bullet-active {
    width: 40px;
    transition: width 0.5s;
    border-radius: 5px;
    background: #000;
    border: 1px solid transparent;
  }
`

// import required modules
import { Pagination } from 'swiper'
import { Link } from 'gatsby'

export default function Example() {
  useEffect(() => {
    AOS.init()
  })

  return (
    <RoomStyle className="bg-gray-100">
      <div
        data-aos="zoom-in"
        data-aos-duration="2000"
        className="mx-auto max-w-2xl px-4 py-8 sm:px-6 sm:py-16 lg:max-w-screen-2xl lg:px-8"
      >
        <h2 className="text-3xl font-bold tracking-tight text-gray-900">
          ROOM
        </h2>
        <p className="mb-8 mt-2 text-gray-500">객실안내</p>

        <div className="w-full">
          <Swiper
            className="flex w-full items-center justify-between rounded-lg"
            slidesPerView={1}
            spaceBetween={10}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              clickable: true,
            }}
            breakpoints={{
              640: {
                slidesPerView: 1,
                spaceBetween: 20,
              },
              768: {
                slidesPerView: 2,
                spaceBetween: 30,
              },
              1024: {
                slidesPerView: 3,
                spaceBetween: 40,
              },
            }}
            modules={[Autoplay, Pagination]}
          >
            <SwiperSlide className="pb-12">
              <Link to="/room/a-type">
                <img
                  className="block h-full w-full rounded-lg object-cover"
                  src="/img/a-type.jpg"
                  alt="a-type room"
                />
                <h3 className="mt-4 text-base font-semibold text-gray-900">
                  A-TYPE 룸
                </h3>
                <p className="mt-2 text-sm text-gray-500">
                  작은 크기의 더블 또는 트윈 침대 / 2~3인
                </p>
              </Link>
            </SwiperSlide>{' '}
            <SwiperSlide>
              <Link to="/room/b-type">
                <img
                  className="block h-full w-full rounded-lg object-cover"
                  src="/img/b-type.jpg"
                  alt="b-type room"
                />
                <h3 className="mt-4 text-base font-semibold text-gray-900">
                  B-TYPE 룸
                </h3>
                <p className="mt-2 text-sm text-gray-500">
                  큰 사이즈의 더블 또는 킹 침대 / 3~4인
                </p>
              </Link>
            </SwiperSlide>{' '}
            <SwiperSlide>
              <Link to="/room/c-type">
                <img
                  className="block h-full w-full rounded-lg object-cover"
                  src="/img/c-type.jpg"
                  alt="c-type room"
                />
                <h3 className="mt-4 text-base font-semibold text-gray-900">
                  C-TYPE 룸
                </h3>
                <p className="mt-2 text-sm text-gray-500">
                  넓은 거실 공간 및 추가 침구 / 6~8인
                </p>
              </Link>
            </SwiperSlide>{' '}
            <SwiperSlide className="pb-12">
              <Link to="/room/a-type">
                <img
                  className="block h-full w-full rounded-lg object-cover"
                  src="/img/a-type.jpg"
                  alt="a-type room"
                />
                <h3 className="mt-4 text-base font-semibold text-gray-900">
                  A-TYPE 룸
                </h3>
                <p className="mt-2 text-sm text-gray-500">
                  작은 크기의 더블 또는 트윈 침대 / 2~3인
                </p>
              </Link>
            </SwiperSlide>{' '}
            <SwiperSlide>
              <Link to="/room/b-type">
                <img
                  className="block h-full w-full rounded-lg object-cover"
                  src="/img/b-type.jpg"
                  alt="b-type room"
                />
                <h3 className="mt-4 text-base font-semibold text-gray-900">
                  B-TYPE 룸
                </h3>
                <p className="mt-2 text-sm text-gray-500">
                  큰 사이즈의 더블 또는 킹 침대 / 3~4인
                </p>
              </Link>
            </SwiperSlide>{' '}
            <SwiperSlide>
              <Link to="/room/c-type">
                <img
                  className="block h-full w-full rounded-lg object-cover"
                  src="/img/c-type.jpg"
                  alt="c-type room"
                />
                <h3 className="mt-4 text-base font-semibold text-gray-900">
                  C-TYPE 룸
                </h3>
                <p className="mt-2 text-sm text-gray-500">
                  넓은 거실 공간 및 추가 침구 / 6~8인
                </p>
              </Link>
            </SwiperSlide>{' '}
          </Swiper>
        </div>
      </div>
    </RoomStyle>
  )
}
